import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-input-tipo-pago',
  templateUrl: './input-tipo-pago.component.html',
  styleUrls: ['./input-tipo-pago.component.css']
})
export class InputTipoPagoComponent implements OnInit {
  
  @Output() seleccionaTipoEvent = new EventEmitter<string>();

  constructor() {
  }
  
  ngOnInit(): void {
  }
  
  seleccionaTipoPago(ev : any | null) {
    this.seleccionaTipoEvent.emit(ev);
  }
  
}
